
* {
  font-family: Calibri, 'Gill Sans', sans-serif;
}

body {
  padding: 50px;
}

input {
  color: green;
  font-size: 1.2em;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-top: 20px;
  margin-bottom: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
